@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400..700&family=Montserrat:ital,wght@0,100..900;1,100..900&family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Pacifico&display=swap');

* {
	margin: 0;
	padding: 0;
}

body {
	background-color: #0bb3f5;
	color: rgb(255, 255, 255);
	text-shadow: 2px 2px 3px #000000;
	padding: 20px;
	font-family: "Caveat", cursive;
	font-optical-sizing: auto;
	font-style: normal;
	font-size: 2rem;
}

.header {
	background-color: #a211e0fb;
	padding: 25px 50px;
	box-shadow: 1px 1px 1px #ffffff;
	font-weight: 600;
	font-size: 3rem;
}

.app {
	display: flex;
	flex-direction: column;
	gap: 10px;
}



.image {
	max-width: 1200px;
}

main {
	margin: 50px 100px;
	width: 50%;
	float: left;
}

aside {
	width: 20%;
	margin-top: 50px;
	margin-left: 5%;
	float: left;
}

main .user {
	margin-bottom: 50px;
	padding: 20px;
	background-color: #e9a6e7fb;
}

main .delite-icon {
	float: right;
	color: #0bb3f5;
	cursor: pointer;
	transform: scale(2);
}

main .edit-icon {
	float: right;
	color: #0bb3f5;
	cursor: pointer;
	margin-right: 35px;
	transform: scale(2);
}

main .style-icon {
	float: right;
	color: #0bb3f5;
	cursor: pointer;
	margin-right: 35px;
	transform: scale(2);
}

main .delite-icon :hover,
main .edit-icon :hover,
main .style-icon :hover {
	opacity: 0.5s;
	color: red;
}

aside form input:not([type="checkbox"]),
aside form textarea,
aside form button {
	width: 90%;
	padding: 15px 5%;
	font-family: "Caveat", cursive;
	font-weight: 400;
	margin-bottom: 10px;
	background: #000;
	color: #ffffff;
	outline: none;
	border: 0;
	font-size: 20px;
}

aside form input[type="checkbox"] {
	margin: 10px;
}

aside form button {
	width: 100%;
	cursor: pointer;
	transition: transform 500ms ease;
}

aside form button:hover {
	transform: scale(1.1);
	border-radius: 10px;
}